<template>
  <LoadingModal v-if="loading"/>
  <form action="#" name="facet_admin_form" enctype="multipart/form-data">
    <div class="main_content">
      <div class="tab_wrapper">
        <div class="tab_container">
          <div id="serverTab" class="tab tab_active" @click="changeConfigType('server')">
            <h3 class="no_margin">サーバー</h3>
          </div>
          <div id="facetTab" class="tab tab_disable" @click="changeConfigType('facet')">
            <h3 class="no_margin">facetCloud</h3>
          </div>
        </div>
      </div>
      <div class="form_wrapper" v-if="config_type == 'server'">
        <div class="form_container">
          <h3>IP設定&emsp;</h3>
          <div class="form_box">
            <div class="ipconfig_container">
              <div class="ip_type_box">
                <label for="ipStatic"><input type="radio" id="ipStatic" name="ip_type" value="static" v-model="ip_type">静的IP</label>
                <label for="ipDHCP"><input type="radio" id="ipDHCP" name="ip_type" value="dhcp" v-model="ip_type">DHCP</label>
              </div>
              <table class="ip_table" v-if="ip_type == 'static'">
                <tr class="ip_tr">
                  <th class="ip_title">IPアドレス(IPv4)：</th>
                  <td>
                    <div class="ip_container">
                      <input type="number" name="ip_1" class="ip_contents" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="ip_2" class="ip_contents" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="ip_3" class="ip_contents" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="ip_4" class="ip_contents" @input="allowNum">
                    </div>
                  </td>
                </tr>
                <tr class="ip_tr">
                  <th class="ip_title">サブネットマスク：</th>
                  <td>
                    <div class="ip_container">
                      <input type="number" name="subnet_1" class="ip_contents" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="subnet_2" class="ip_contents" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="subnet_3" class="ip_contents" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="subnet_4" class="ip_contents" @input="allowNum">
                    </div>
                  </td>
                </tr>
                <tr class="ip_tr">
                  <th class="ip_title">DNS：</th>
                  <td>
                    <div class="ip_container">
                      <input type="number" name="gw_1" class="ip_contents" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="gw_2" class="ip_contents" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="gw_3" class="ip_contents" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="gw_4" class="ip_contents" @input="allowNum">
                    </div>
                  </td>
                </tr>
                <tr class="ip_tr">
                  <th class="ip_title">DNS：</th>
                  <td>
                    <div class="ip_container">
                      <input type="number" name="dns_1" class="ip_contents" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="dns_2" class="ip_contents" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="dns_3" class="ip_contents" @input="allowNum">
                      <div class="ip_dot">●</div>
                      <input type="number" name="dns_4" class="ip_contents" @input="allowNum">
                    </div>
                  </td>
                </tr>
              </table>
            </div>
            <div>
              <div class="submit_btn" @click="ipUpdate()">更新</div>
            </div>
          </div>
        </div>
        <hr size="10">
        <div class="form_container">
          <h3>NTP</h3>
          <input v-if="type !== 'add'" type="text" name="login_id" class="main_text disable_content" :value="login_id">
          <input v-else type="text" name="login_id" class="main_text">
        </div>
        <hr size="10">
        <div class="form_container">
          <h3>ミドルウェアアップデート</h3>
          <div class="form_box">
            <div class="middleware_container">
              <input type="file" name="middleware_file">
            </div>
            <div>
              <div class="submit_btn" @click="updateMiddleware()">更新</div>
            </div>
          </div>
        </div>
        <hr size="10">
        <div class="form_container">
          <h3>手動再起動</h3>
          <div class="form_box">
            <div>
              <p class="no_margin form_info">サーバーを再起動します。</p>
              <p class="no_margin form_info">再起動時は全ての処理が中断され接続ができません。</p>
            </div>
            <div>
              <div class="submit_btn" @click="execReboot()">実行</div>
            </div>
          </div>
        </div>
        <hr size="10">
        <div class="form_container">
          <h3>自動再起動</h3>
          <div class="form_box">
            <div>
              <div>
                <p class="no_margin form_info">指定のスケジュールで自動でサーバーを再起動します。</p>
                <p class="no_margin form_info">再起動時は全ての処理が中断され接続ができません。</p>
              </div>
              <div class="reboot_container">
                <div class="reboot_enable">
                  <label for="rebootDisable"><input type="radio" id="rebootDisable" name="reboot_enable" value="disable" v-model="reboot_type">無効</label>
                  <label for="rebootEnable"><input type="radio" id="rebootEnable" name="reboot_enable" value="enable"  v-model="reboot_type">有効</label>
                </div>
                <label v-if="reboot_type == 'enable'" class="reboot_week" for="rebootWeek">曜日
                  <select name="reboot_week" id="rebootWeek" v-model="reboot_week">
                    <option v-for="week in weekArray" :value="week.value" v-bind:key="week.id">{{ week.name }}</option>
                  </select>
                </label>
                <label v-if="reboot_type == 'enable'" class="reboot_hour" for="rebootHour">時間
                  <input type="number" id="rebootHour" placeholder="0" name="reboot_hour" v-model="reboot_hour" @input="rebootHourControl">時
                </label>
              </div>
            </div>
            <div>
              <div class="submit_btn" @click="execRebootSchedule()">実行</div>
            </div>
          </div>
        </div>
        <hr size="10">
      </div>
      <div class="form_wrapper" v-if="config_type == 'facet'">
        <div class="form_container">
          <h3>facetCloudバージョンアップ</h3>
          <div class="form_box">
            <div class="middleware_container">
              <input type="file">
            </div>
            <div>
              <div class="submit_btn">更新</div>
            </div>
          </div>
        </div>
        <hr size="10">
        <div class="form_container">
          <h3>ログ取得</h3>
          <div class="form_box">
            <div>
              <p class="no_margin form_info">指定があった場合にのみ取得を行ってください。</p>
            </div>
            <div>
              <div class="submit_btn">取得</div>
            </div>
          </div>
        </div>
        <hr size="10">
        <div class="form_container">
          <h3>入出力処理の排他制御強制解除</h3>
          <div class="form_box">
            <div>
              <p class="no_margin form_info">エクスポートやインポートの処理中に実行すると処理が失敗します。</p>
              <p class="no_margin form_info">必ず必要な場合にのみ実行を行ってください。</p>
            </div>
            <div>
              <div class="submit_btn">実行</div>
            </div>
          </div>
        </div>
        <hr size="10">
      </div>
    </div>
  </form>
</template>

<script>
const axios = require('axios')
import LoadingModal from '@/components/LoadingModal.vue'

export default {
  name: 'TopPage',
  components: {
    LoadingModal
  },
  data () {
    return {
      config_type: 'server',
      ip_type: 'static',
      reboot_type: 'disable',
      reboot_week: 'sun',
      reboot_hour: '0',
      weekArray: [
        {id: "1", value:"sun", name:"日曜日"},
        {id: "2", value:"mon", name:"月曜日"},
        {id: "3", value:"tue", name:"火曜日"},
        {id: "4", value:"wed", name:"水曜日"},
        {id: "5", value:"thu", name:"木曜日"},
        {id: "6", value:"fri", name:"金曜日"},
        {id: "7", value:"sat", name:"土曜日"},
        {id: "8", value:"every", name:"毎日"},
      ],
      type: '',
      ee_flag: false,
      contractors: [],
      contractor: null,
      count: 0,
      user: [],
      users: [],
      login_id: '',
      form_infos: null,
      origin_infos: null,
      modal_state: false,
      error: '',
      loading: false
    }
  },
  mounted () {
    let self = this
    // IP設定の取得・反映
    axios
      .get(location.origin + '/server/backend/system/ipconfig')
      .then(function (response) {
        console.log(response.data)
      })
    // 自動再起動設定の取得・反映
    axios
      .get(location.origin + '/server/backend/system/rebootSchedule')
      .then(function (response) {
        console.log(response.data)
        // タイプの変更
        self.reboot_type = response.data.reboot
        if (response.data.reboot === 'enable') {
          // 有効の場合は設定を反映
          self.reboot_week = (response.data.week === '*') ? 'every': response.data.week
          self.reboot_hour = response.data.hour
        }
      })
  },
  methods: {
    // IP設定の入力時
    allowNum: function (e) {
      // ドットが入力されたら次のフォームへフォーカス
      if (e.data == '.' && e.target.name.split('_')[1] != 4) {
        let next_class = e.target.name.split('_')[0]+'_'+(Number(e.target.name.split('_')[1])+1)
        document.getElementsByName(next_class)[0].focus()
      }

      let input_len = e.target.value.length
      // 3桁制限
      e.target.value = e.target.value.slice(0, 3)

      // 3桁入力されたら次のフォームへフォーカス
      if (input_len == 3) {
        if (e.target.name.split('_')[1] != 4) {
          let next_class = e.target.name.split('_')[0]+'_'+(Number(e.target.name.split('_')[1])+1)
          document.getElementsByName(next_class)[0].focus()
        }
      }
    },
    ipUpdate: function () {
      const form = document.facet_admin_form;

      let params = [];
      params["ip"] = [form.ip_1.value, form.ip_2.value, form.ip_3.value, form.ip_4.value]
      params["subnet"] = [form.subnet_1.value, form.subnet_2.value, form.subnet_3.value, form.subnet_4.value]
      params["gw"] = [form.gw_1.value, form.gw_2.value, form.gw_3.value, form.gw_4.value]
      params["dns"] = [form.dns_1.value, form.dns_2.value, form.dns_3.value, form.dns_4.value]

      axios
        .post(location.origin + '/server/backend/system/ipconfig', params)
        .then(function (response) {
          console.log(response.data)
        })
    },
    rebootHourControl: function (e) {
      if (e.target.value > 23) {
        e.target.value = 23
      } else if (e.target.value < 0) {
        e.target.value = 0
      }
    },
    // タブ操作時
    changeConfigType: function (type) {
      // 設定種類の変更
      this.config_type = type
      // タブのクラスの変更
      const targetIdname = type+'Tab';
      let tabElements = document.getElementsByClassName('tab')
      for (let i = 0; i < tabElements.length; i++) {
        if (tabElements[i].id !== targetIdname) {
          tabElements[i].classList.add('tab_disable')
          tabElements[i].classList.remove('tab_active')
        } else {
          tabElements[i].classList.remove('tab_disable')
          tabElements[i].classList.add('tab_active')
        }
      }
    },
    updateMiddleware: function () {

      const params = new FormData();
      params.append("middleware_file", document.facet_admin_form.middleware_file.files[0]);

      if (document.facet_admin_form.middleware_file.files[0]) {
        axios
          .post(location.origin + '/server/backend/system/middleware', params, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          })
          .then(function (response) {
            console.log(response.data)
          })
        
      } else {
        alert("アップロードファイルが見つかりません")
      }
    },
    execReboot: function () {
      axios
        .post(location.origin + '/server/backend/system/reboot/manual', "")
        .then(function (response) {
          console.log(response.data)
        })
    },
    execRebootSchedule: function () {
      let form = document.facet_admin_form

      // 送信パラメータ格納
      let params;
      if (form.reboot_enable.value === 'enable') {
        params = {
          'reboot_enable': form.reboot_enable.value,
          'reboot_week': form.reboot_week.value,
          'reboot_hour': (form.reboot_hour.value) ? form.reboot_hour.value : 0,
        }
      } else {
        params = {
          'reboot_enable': form.reboot_enable.value
        }
      }

      axios
        .post(location.origin + '/server/backend/system/reboot/auto', params)
        .then(function (response) {
          console.log(response.data)
        })
        .finally(function () {
          // 初期画面へリダイレクト
          window.location.reload()
        })

    },
    getContractors: function () {
      let self = this

      if (self.tenantval !== '') {
        axios
          .get(location.origin + '/server/backend/contractor/' + self.tenantval)
          .then(function (response) {
            self.contractors = response.data
            self.count = 0

            for (let i = 0; i < self.contractors.length; i++) self.count += self.contractors[i].allow_device_num
          })
        
        // userの取得
        axios
          .get(location.origin + '/server/backend/user/' + self.tenantval)
          .then(function (response) {
            self.users = response.data
          })
      }

      self.valueInit()
    },
    selectContractor: function (e) {
      let self = this

      // フォームと格納されている変数初期化
      self.valueInit()
      self.form_infos = null

      // contractorの取得
      for (let i = 0; i < self.contractors.length; i++) {
        if (self.contractors[i].contractor_id === Number(e.target.value)) {
          // 一致したら格納・フラグ更新して終了
          self.contractor = self.contractors[i]
          self.ee_flag = Boolean(self.contractor.enter_exit_mode_flag)
          break
        } else {
          // 一致しない場合は続ける
          continue
        }
      }

      // userの取得
      axios
        .get(location.origin + '/user/backend/user/' + self.tenantval + '/' + e.target.value)
        .then(function (response) {
          self.user = response.data
          self.login_id = response.data.login_id
        })
    },
    valueInit: function (change = false) {
      let self = this

      // 変数の初期化
      self.contractor = null
      self.user = []
      self.login_id = ''
      self.ee_flag = false

      // 値の初期化
      if (document.facet_admin_form.login_id) {
        document.facet_admin_form.allow_device_num.value = ''
        document.facet_admin_form.domain.value = ''
        document.facet_admin_form.apb_mode_flag.checked = false
        document.facet_admin_form.ee_mode_flag.checked = false
        document.facet_admin_form.api_allow_ip.value = ''
        document.facet_admin_form.trial_limit.value = ''
        document.facet_admin_form.login_id.value = ''
        document.facet_admin_form.user_name.value = ''
        document.facet_admin_form.allow_ips.value = ''
        if (document.getElementById('eeDescBox')) {
          document.facet_admin_form.ee_description_name_1.value = ''
          document.facet_admin_form.ee_description_name_2.value = ''
        }
      }

      // fix_typeをeditもしくはdeleteに変更した場合にcontractor_idを初期化
      if (change && self.type !== 'add') document.facet_admin_form.contractor_id.value = ''
    },
    changeEEStatus: function () {
      let self = this
      let form = document.facet_admin_form
      let ee_check = document.getElementById('checkEE').checked

      // ee_flag変更前にform状況を格納
      if (self.type === 'edit') {
        self.form_infos = {
          'allow_device_num' : form.allow_device_num.value,
          'domain' : form.domain.value,
          'api_allow_ip' : form.api_allow_ip.value,
          'trial_limit' : self.datetimeConversion(form.trial_limit.value, 'param'),
          'allow_ips' : form.allow_ips.value
        }
      }

      // チェックステータスを反映
      self.ee_flag = ee_check
    },
    openModal: function () {
      let self = this
      let form = document.facet_admin_form

      // tenantval、type選択しているか
      if (self.type !== 'add' && form.contractor_id.value == '') {
        alert('契約名が選択されていません')
      } else {
        // フォーム内容の格納
        if (form.fix_type.value !== 'delete' && form.fix_type.value !== 'init') {
          let contractor_name = (form.fix_type.value == 'add') ? form.contractor_name.value : self.contractor.contractor_name
          self.form_infos = {
            'fix_type' : form.fix_type.value,
            'contractor_name' : contractor_name,
            'allow_device_num' : (form.allow_device_num.value) ? Number(form.allow_device_num.value) : null,
            'domain' : (form.domain.value) ? form.domain.value : null,
            'apb_mode_flag' : (form.apb_mode_flag.checked) ? 1 : 0,
            'ee_mode_flag' : (form.ee_mode_flag.checked) ? 1 : 0,
            'ee_desc_1' : (form.ee_mode_flag.checked && form.ee_description_name_1.value) ? form.ee_description_name_1.value : null,
            'ee_desc_2' : (form.ee_mode_flag.checked && form.ee_description_name_2.value) ? form.ee_description_name_2.value : null,
            'api_allow_ip' : (form.api_allow_ip.value) ? form.api_allow_ip.value : null,
            'trial_limit' : (form.trial_limit.value) ? self.datetimeConversion(form.trial_limit.value, 'param') : null,
            'login_id' : (form.login_id.value) ? form.login_id.value : null,
            'user_name' : (form.user_name.value) ? form.user_name.value : null,
            'allow_ips' : (form.allow_ips.value) ? form.allow_ips.value : null
          }
        } else {
          self.form_infos = {
            'fix_type' : form.fix_type.value,
            'contractor_name' : self.contractor.contractor_name
          }
        }
  
        // 既存内容の整理・格納
        if (form.fix_type.value !== 'add') {
          self.origin_infos = {
            'contractor_id' : self.contractor.contractor_id,
            'contractor_name' : self.contractor.contractor_name,
            'allow_device_num' : self.contractor.allow_device_num,
            'domain' : self.contractor.domain,
            'apb_mode_flag' : (self.contractor.apb_mode_flag) ? 1 : 0,
            'ee_mode_flag' : (self.contractor.enter_exit_mode_flag) ? 1 : 0,
            'ee_desc_1' : self.contractor.enter_exit_description_name1,
            'ee_desc_2' : self.contractor.enter_exit_description_name2,
            'api_allow_ip' : self.contractor.api_allow_ip,
            'trial_limit' : self.contractor.trial_limit,
            'login_id' : self.user.login_id,
            'user_name' : self.user.user_name,
            'allow_ips' : self.user.allow_ips
          }
        } else {
          self.origin_infos = null
        }
  
        self.modal_state = true
      }
    },
    datetimeConversion: function (datetime, type) {
      if (!datetime) {
        return datetime
      } else {
        if (type === 'value') {
          // フォームバリューとして使用したい場合
          let datetime_array = datetime.split(' ')
          let time_array = datetime_array[1].split(':')
          return datetime_array[0] + 'T' + time_array[0] + ':' + time_array[1]
        } else {
          // パラメータもしくは表示用として使用したい場合
          return datetime.replace('T', ' ') + ':00'
        }
      }
    },
    confirmPermission: function () {
      return document.getElementById('basicLoginUser').textContent
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h2, h3 {
  margin: 0;
  text-align: start;
}

input, select {
  padding: 0.5rem 1rem;
  border-radius: 10px;
}

/* input, select, label {
  font-size: 1.2rem;
} */

select {
  width: fit-content;
}

input[type=text] {
  box-sizing: border-box;
}
input[type=checkbox] {
  height: 30px;
  width: 30px;
  cursor: pointer;
}
input[type=datetime-local] {
  box-sizing: border-box;
}

textarea {
  resize: none;
}

.relative {
  position: relative;
}

.main_content {
  background-color: #fff;
  box-sizing: border-box;
  padding: 1rem 100px;
  width: 100%;
}

.form_wrapper {
  padding: 2rem;
  box-sizing: border-box;
  border: 2px solid #003da6;
}

.form_container {
  width: 100%;
  margin: 1.5rem 0 3rem 0;
}
.form_container:first-child {
  margin: 0 0 3rem 0;
}

.check_container {
  align-items: center;
  display: flex;
  width: 40%;
  margin: 1rem 0;
}
.check_container > h3 {
  width: 70%;
}
.main_text {
  width: 100%;
  min-width: 300px;
}

.ee_desc_box {
  align-items: center;
  display: flex;
  width: 50%;
}
.ee_desc_box > input:nth-child(2) {
  margin-left: 1rem;
}
.sub_text {
  width: 150px !important;
}

.required {
  color: red;
  font-weight: bold;
}

.flex_box {
  align-items: center;
  display: flex;
}

.submit_box {
  display: flex;
  justify-content: center;
}

.allow_device {
  font-size: 0.9rem;
  margin: 0;
  position: absolute;
  bottom: 10%;
  left: calc(150px + 10%);
}
.allow_device_count {
  font-size: 1.2rem;
  font-weight: bold;
}



.form_box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.form_box > div:first-child {
  padding-left: 1rem;
}

.form_info {
  font-size: 0.8rem;
  text-align: start;
}

/* IP設定関連 */
input[type="number"]::-webkit-outer-spin-button, 
input[type="number"]::-webkit-inner-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
